  getIP(mac) {
    let main = this;
    console.log(mac);
    console.log(mac.S);
    return main.http.post<any>(main.link, { "job": "get_ip", mac: mac }, { headers: this.headers }).pipe(
      tap((result) => {
        console.log("get ip result", result);
      })
    );
  }

  getIP2(mac) {
    let main = this;
    console.log(mac);
    console.log(mac.S);
    main.selected_device_mac_address = mac.S;
    return main.http.post<any>(main.link, { "job": "get_ip", mac: mac.S }, { headers: this.headers }).pipe(
      tap((result) => {
        console.log("get ip result", result);
      })
    );
  }

  sendIP() {
    let main = this;
    return main.http.post<any>(main.link, { "job": "send_ip" }, { headers: this.headers }).pipe(
      tap((result) => {
        console.log(result);
      })
    );
  }

  deleteDevices(id, deviceid) {
    let main = this;
    return main.http.post<any>(main.link, { "job": "delete_moka_devices", id: id, deviceid: deviceid }, { headers: this.headers }).pipe(
      tap((result) => {
        console.log(result);
      })
    );
  }

  setWifi(ssid, pass) {
    let main = this;
    return main.http.post<any>(main.set_url + '/setwifi', { "ssid": ssid, "pass": pass }, { headers: main.headers }).pipe(
      tap((result) => {
        console.log(result);
      })
    )
  }

  // setWifi(ssid, pass) {
  //   let main = this;
  //   return main.http.post<any>(main.set_url + '/setwifi?ssid=' + ssid + '&pass=' + pass, null, { headers: main.headers }).pipe(
  //     tap((result) =>{
