  setWifiStatusDone(statusDone) {
    let main = this;
    return main.http.post<any>(main.set_url + '/statusdone?statusDone=' + statusDone, null, { headers: main.headers }).pipe(
      tap((result) => {
        console.log("status done: ", result);
      })
    )
  }

  constructor(
    private http: HttpClient
  ) { }

  ngOnInit() {
    this.headers = new HttpHeaders();
    this.headers.append('Accept', 'application/json');
    this.headers.append('Content-Type', 'application/json');
    this.headers.append('Access-Control-Allow-Origin', '*');
  }

  getMacAddress() {
    let main = this;
    var newStr = '';
    var str = '';
    if (str.length > 30) {

    }
    else {
      return this.http.post(this.mac_url, null).pipe(
        tap(data => {
          main.mokapot_mac_data = JSON.stringify(data);
          str = main.mokapot_mac_data;
          newStr = str.substring(11, 29);
          main.mokapot_mac_address = newStr;
          //console.log("mac adress: ", main.mokapot_mac_address);
        }),
        catchError(this.handleError('get mac address'))
      )
    }
  }

  getWifiList() {
    return this.http.get(this.url).pipe(
      tap(data => {
        this.esp_wifi = data;
        this.esp_wifi_list = this.esp_wifi.list
      }),
      catchError(this.handleError('get wifi'))
    );
  }

  getWifiStatus() {
    return this.http.get(this.network_status_url).pipe(
      tap(data => {
        console.log("get wifi status", data);
        this.esp_network_status = data;
      }),
      catchError(this.handleError('get wifi status'))
    );
  }

  addMokapotDevicesControlQuery(userid, device) {
    let main = this;
    return main.http.post<any>(main.link, { "job": "add_moka_devices_control_query", userid: userid, device: device }, { headers: this.headers }).pipe(
      tap((result) => {
        console.log(result);
      }, catchError(this.handleError('add_moka_devices_control_query control')))
    );
  }

  addMokapotDevicesControlQueryData() {
    let main = this;
    return main.http.post<any>(main.link, { "job": "add_moka_devices_control_query_data" }, { headers: this.headers }).pipe(
      tap((result) => {
        console.log(result);
      })
    );
  }

  addMokapotDevicesControl() {
    return this.http.post<any>(this.link, { "job": "add_moka_devices_control" }).pipe(
      tap((result) => { console.log("add_moka_devices_control", result) }),
      catchError(this.handleError('add user mokapot devices control'))
    );
  }

