  deleteDevice() {
    let main = this;
    return main.http.post<any>(main.set_temp_url + '/deletedevice', null, { headers: main.headers }).subscribe((data) => {
      console.log("delete device: ", data);
      main.connected_network = '';
    }), (err) => {
      console.log("error", err);
    }
  }

  getStatus() {
    let main = this;
    return main.http.post<any>(main.set_temp_url + '/status', null, { headers: main.headers }).subscribe((data) => {
      console.log("cur temp data: ", data);
      main.device_status = data.status;
      main.status_data = data;
    }), (err) => {
      console.log("error", err);
    };
  }

  getStatusBasicMoka(){
    let main = this;
    return main.http.post<any>(main.set_temp_url + '/status', null, { headers: main.headers }).subscribe((data) => {
      console.log("data");
      main.device_status = data.status;
      main.status_data = data;
    }), (err) => {
      console.log("error", err);
    };
  }

  private handleError<T>(operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
      console.log('Error on:', operation);
      console.error(error);
      return of(result as T);
    };
  }
